import { css } from '@emotion/css';
import { mobileMediaQuery } from 'components';
import { generalMenuWidthPX, sheetWidthPX, fadeInKeyFrames } from '../consts';

export const headerMenuStyle = (isMenuOpened: boolean, withMenu: boolean): string => css({
  backgroundColor: '#fff',
  position: 'fixed',
  top: 0,
  left: 0,
  width: isMenuOpened ? sheetWidthPX : generalMenuWidthPX,
  height: isMenuOpened ? '100%' : withMenu ? '118px' : undefined,
  transition: 'width 200ms ease-in, margin 200ms ease-in, height 200ms ease-in',
  boxSizing: 'border-box',
  display: 'flex',
  justifyContent: 'space-between',
  flexDirection: 'column',
  padding: '20px',
  margin: `0 calc(50% - ${(isMenuOpened ? sheetWidthPX : generalMenuWidthPX) / 2}px)`,
  zIndex: 10,

  [mobileMediaQuery]: {
    width: isMenuOpened ? '100%' : generalMenuWidthPX,
    margin: isMenuOpened ? '0 0' : `0 calc(50% - ${generalMenuWidthPX / 2}px)`
  }
});

export const menuItemsStyle = css({
  opacity: 0,
  animation: `${fadeInKeyFrames} 200ms ease-in 300ms forwards`
});
