import React from 'react';
import { buttonWrapper, textFolderStyle } from './text-folder.style';
import { Text } from '../text';

type TTextFolderProps = {
  children: React.ReactNode;
  buttonColor: 'red' | 'white';
};

export function TextFolder({ children, buttonColor }: TTextFolderProps): JSX.Element {
  const [isFolded, setIsFolded] = React.useState(true);

  const setIsUnfolded = React.useCallback(() => setIsFolded(false), []);

  return (
    <div>
      <div className={textFolderStyle(isFolded)}>{children}</div>
      <div className={buttonWrapper(isFolded)}>
        <Text size="l" color={buttonColor} onClick={setIsUnfolded}>more</Text>
      </div>
    </div>
  );
}
