import { TProjectModel } from 'common';
import { BottomMenu, Layout, ProjectItem } from 'components';
import React from 'react';
import { allProjectsPageStyle, projectItemWrapperStyle } from './all-projects-page.style';
import { CurrentHashTag } from './current-hashtag';
import { HashtagsSheet } from './hashtags-sheet';

type TAllProjectsPageProps = {
  projects: Array<TProjectModel>;
};

export function AllProjectsPageContent({ projects }: TAllProjectsPageProps): JSX.Element {
  return (
    <Layout>
      <div className={allProjectsPageStyle}>
        {projects.map((project: TProjectModel) => (
          <div key={project.projectName} className={projectItemWrapperStyle}>
            <ProjectItem
              id={project.project}
              projectName={project.projectName}
              imageUrl={project.photoUrl}
            />
          </div>
        )
        )}
      </div>
      <BottomMenu
        menuClosedContent={<CurrentHashTag />}
        menuOpenedContent={<HashtagsSheet />}
      />
    </Layout>
  );
}
