import { css } from '@emotion/css';
import { mobileMediaQuery, copyrightHeightPX, headerMenuHeightPX } from '../consts';

export const layoutStyle = (isContentFullLayout?: boolean, noFooter?: boolean): string => css({
  width: '100%',
  minHeight: noFooter ? '100%' : `calc(100% - ${copyrightHeightPX}px)`,
  height: noFooter ? '100%' : undefined,
  boxSizing: 'border-box',
  overflow: 'scroll',
  padding: isContentFullLayout ? '0' : `${headerMenuHeightPX}px 0 0`
});

export const footerStyle = css({
  width: '100%',
  display: 'flex',
  justifyContent: 'center'
});

export const layoutContentStyle = (height?: string): string => css({
  width: '100%',
  height,
  boxSizing: 'border-box',
  padding: '0 40px',

  [mobileMediaQuery]: {
    padding: '0 20px'
  }
});
