import React from 'react';
import { footerStyle, layoutStyle } from './layout.style';
import { Colors } from '../colors';
import { CopyRight } from '../copyright';

type TLayoutProps = {
  children: React.ReactNode;
  isContentFullLayoutView?: boolean;
  bgrColor?: Colors;
  noFooter?: boolean;
};

export function Layout({
  children,
  bgrColor,
  isContentFullLayoutView = true,
  noFooter = false
}: TLayoutProps): JSX.Element {
  React.useEffect(() => {
    document.body.style.backgroundColor = bgrColor || Colors.BLACK;
  }, [bgrColor]);

  return (
    <>
      <main className={layoutStyle(isContentFullLayoutView, noFooter)}>
        {children}
      </main>
      {!noFooter && (
        <footer className={footerStyle}>
          <CopyRight />
        </footer>
      )}
    </>
  );
}
