import { css } from '@emotion/css';
import { TTextProps } from './text.props-type';
import { Colors } from '../colors';

type TRequiredTextProps = Required<TTextProps>;

const textSizeMap: Record<TRequiredTextProps['size'], string> = {
  'l': '30px',
  'm': '10px'
};

const lineHeightMap: Record<TRequiredTextProps['size'], string> = {
  'l': '27px',
  'm': '14px'
};

const textColorMap: Record<TRequiredTextProps['color'], string> = {
  'black': Colors.BLACK,
  'white': Colors.WHITE,
  'red': Colors.RED
};

const fontFamilyMap: Record<TRequiredTextProps['size'], string> = {
  'l': 'HelveticaNeue-Bold, HelveticaNeueLTCYR-BdCond',
  'm': 'HelveticaNeueCyr-Bold'
};

export const textStyle = (
  size: TRequiredTextProps['size'],
  color: TRequiredTextProps['color'],
  action: boolean,
  align: TRequiredTextProps['align'],
  cursor: TTextProps['cursor']
): string => css({
  fontSize: textSizeMap[size],
  fontFamily: fontFamilyMap[size],
  color: textColorMap[color],
  cursor: cursor ? cursor : action ? 'pointer' : 'auto',
  textAlign: align,
  lineHeight: lineHeightMap[size],
  letterSpacing: size === 'm' ? '0.6px' : undefined
});
