import { css } from '@emotion/css';
import { mobileMediaQuery } from 'components';

export const textFolderStyle = (isFolded: boolean): string => css({
  width: '100%',
  height: '100%',

  [mobileMediaQuery]: {
    height: isFolded ? '485px' : '100%',
    overflow: 'hidden'
  }
});

export const buttonWrapper = (isFolded: boolean): string => css({
  visibility: 'hidden',

  [mobileMediaQuery]: {
    visibility: isFolded ? 'visible' : 'hidden'
  }
});
